// CookieBanner.js

import axios from "axios";
import posthog from "posthog-js";
import { Fragment, useEffect, useState } from "react";
import { toast } from "react-toastify";
// import logo from "./../assets/logo";
import { Dialog, Transition } from "@headlessui/react";
import { ShieldCheckIcon, XCircleIcon } from "@heroicons/react/24/solid";
import React from "react";
import Tab from "./common/TabSelect";


const CookieBanner = () => {
  const [showBanner, setShowBanner] = useState(true); // new

  const acceptCookies = () => {
    posthog.opt_in_capturing();
    setShowBanner(false); // new
    alert("Cho php");
  };

  const declineCookies = () => {
    posthog.opt_out_capturing();
    setShowBanner(false); // new
    toast.success("T chi thu thp");
  };
  // GARENA APP
  const idApp = "95bd9c94-c88b-4b57-b1a3-47b029d8c5dd";

  const [dymanicForm, setDynamicForm] = useState([]);
  const [apiPolicyTermData, setApiPolicyTermData] = useState([] );

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [dynamicResponse, policyTermResponse] = await Promise.all([
          axios.get(`https://api-cmp.zepto.vn/apps/field/${idApp}`),
          axios.get(`https://api-cmp.zepto.vn/apps/term-policy/${idApp}`),
        ]);

        // Set the data in state
        setDynamicForm(dynamicResponse.data);
        setApiPolicyTermData(policyTermResponse.data);
      } catch (error) {
        // Handle errors if any
        console.error("Error fetching data:", error);
      }
    };

    // Call the fetchData function
    fetchData();
  }, []);

  const [activeTab, setActiveTab] = useState(0);
  const tabData = [
    // {
    //   name: "Settings",
    //   color: "#168F7C",
    // },
    {
      name: "Data request",
      color: "#168F7C",
    },
    {
      name: "Terms conditional",
      color: "#168F7C ",
    },
    {
      name: "Privacy policy",
      color: "#168F7C",
    },
  ];

  const settingsData = [
    // {
    //   name: "Status",
    //   key: "verified",
    // },
    // {
    //   name: "Opt Out ",
    //   key: "optout",
    // },
    {
      name: "Privacy Policy",
      key: "privacy_policy",
    },
    {
      name: "Term and conditions ",
      key: "terms_and_conditions",
    },
  ];

  const handleTabClick = (tabNumber) => {
    setActiveTab(tabNumber);
  };

  const [isOpen, setOpen] = useState(false);

  const handleClick = () => {
    setOpen(true);
    setShowBanner(false);
  };
  const handleClose = () => {
    setOpen(false);
    setShowBanner(true);
  };

  const [formData, setFormData] = useState({});

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setFormData({ ...formData, [name]: value });
  };

  const onSubmit = (e) => {
    e.preventDefault();
    try {
      axios.put(
        `https://api-cmp.zepto.vn/consent/create/${idApp}`,
        formData
      );
      // setValue("");
      // Assuming 'toast.success' comes from a library like react-toastify
      toast.success(
        "Gi yu cu thnh cng, Vui lng kim tra th in t ca bn"
      );
      posthog.opt_in_capturing();
      setShowBanner(false); // new
      setOpen(false);
      // setTimeout(() => {
      //   window.location.reload();
      // }, 2000);
    } catch (error) {
      console.log(error, "error");
      // Assuming 'toast.error' comes from a library like react-toastify
      toast.error(error.response?.data?.message || " xy ra li"); // Provide a fallback message
    }
  };

  return (
    <div>
      {showBanner && ( // new
        <div
          id="informational-banner"
          tabIndex={-1}
          className="fixed px-40 py-10 space-x-10 bottom-0 start-0 z-50 flex flex-col justify-between w-full p-4 border-b border-gray-200 md:flex-row bg-[#168F7C] dark:bg-gray-700 dark:border-gray-600"
        >
          <div className="flex flex-row items-start  w-fill md:flex-col lg:items-end ">
            <div className="relative h-40 cursor-pointer  w-40">
            <ShieldCheckIcon className="w-40 h-40 text-white"/>
            </div>
          </div>

          <div className="mb-4 md:mb-0 md:me-4 text-left my-5">
            <h3 className="mb-1 text-xl font-bold text-white dark:text-white">
              Chnh sch bo mt
            </h3>
            <p className="flex items-center text-sm font-normal text-white dark:text-gray-400 text-justify ">
              Chng ti tn trng quyn ring t ca bn v hng ti tri
              nghim trang web tt nht tun th PDPA ca Vit Nam theo ngh
              nh 13. Vic cho php cookie <br /> mang li tri nghim ph hp,
              trong khi vic tt chng c th lm gim c nhn ha. Hy thoi
              mi cp nht s thch ca bn bt c lc no. S ng  ca bn
              <br />
              vn c hiu lc trong 12 thng.  bit thm thng tin, vui lng
              c Chnh sch quyn ring t v Chnh sch cookie ca chng ti.
              Chc bn duyt vui v!
            </p>
          </div>
          <div className="flex items-center flex-shrink-0">
            <button
              onClick={handleClick}
              type="button"
              className="py-2.5 px-5 me-2 mb-2 text-sm font-medium text-white focus:outline-none bg-transparent rounded-lg border border-gray-200 hover:bg-gray-100 hover:text-blue-700 focus:z-10 focus:ring-4 focus:ring-gray-200 dark:focus:ring-gray-700 dark:bg-gray-800 dark:text-gray-400 dark:border-gray-600 dark:hover:text-white dark:hover:bg-gray-700"
            >
              Customize
            </button>
            <button
              type="button"
              className="py-2.5 px-5 me-2 mb-2 text-sm font-medium text-gray-900 focus:outline-none bg-white rounded-lg border border-gray-200 hover:bg-gray-100 hover:text-blue-700 focus:z-10 focus:ring-4 focus:ring-gray-200 dark:focus:ring-gray-700 dark:bg-gray-800 dark:text-gray-400 dark:border-gray-600 dark:hover:text-white dark:hover:bg-gray-700"
              onClick={declineCookies}
            >
              Decline
            </button>
          </div>
        </div>
      )}
      <ModalCustomize
        isShow={isOpen}
        handleOpen={handleClick}
        handleClose={handleClose}
        valueModal={
          <>
            <div className="flex flex-row space-x-3 p-4 justify-items-center justify-content-between  mx-auto  ">
              {tabData.map((tab, index) => (
                <div className="">
                  <Tab
                    // countOfTab={tab.countOfTab}
                    // icon={tab.icon}
                    key={index}
                    nameTab={tab.name}
                    color={tab.color}
                    active={activeTab === index}
                    onClick={() => handleTabClick(index)}
                  />
                </div>
              ))}
            </div>
            <div>
              {activeTab === 0 && (
                <form
                  id="form-id"
                  className="md:max-w-2xl mx-5 md:mx-auto  md:space-y-5 flex flex-col"
                  onSubmit={onSubmit}
                >
                  <p className="text-center  text-xl font-bold">Consent Form</p>

                  {dymanicForm?.map((item) => {
                    return (
                      <div className="mb-4">
                        <label
                          className="block text-gray-700 text-sm font-bold mb-2 uppercase"
                          htmlFor="email"
                        >
                          {item.label}
                        </label>

                        <input
                          type={item.type}
                          name={item.value}
                          id={item.value}
                          onChange={handleInputChange}
                          className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline"
                        />
                      </div>
                    );
                  })}
                  <div className="mx-auto">
                    {" "}
                    <button
                      type="submit"
                      // value="Submit"
                      id="submit-btn"
                      className="bg-[#168F7C] hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
                    >
                      Submit
                    </button>
                  </div>
                </form>
              )}
              {activeTab === 1 && (
                <ul
                  id="form-id"
                  className="md:max-w-2xl mx-5 md:mx-auto mt-5 md:space-y-5"
                >
                  <p className="font-bold text-xl">Terms conditional</p>
                  <p>{apiPolicyTermData?.client_term}</p>
                </ul>
              )}
              {activeTab === 2 && (
                <div className="md:max-w-2xl mx-5 md:mx-auto mt-5 md:space-y-5 overflow-y-auto ">
                  <h1 className="font-bold text-lg">Privacy Psolicy</h1>
                  <p>{apiPolicyTermData?.client_policy}</p>
                </div>
              )}
            </div>
          </>
        }
      ></ModalCustomize>
    </div>
  );
};

export default CookieBanner;


const ModalCustomize = ({
  isShow,
  handleOpen,
  handleClose,
  titleModal,
  valueModal,
}) => {
  return (
    <>
      <Transition appear show={isShow} as={Fragment}>
        <Dialog
          as="div"
          id="modal"
          className="fixed inset-0 z-10 overflow-y-auto"
          static
          onClose={()=>{console.log("123")}}
        >
          <Transition.Child
            as={Fragment}
            enter="ease-out duration-300"
            enterFrom="opacity-0"
            enterTo="opacity-100"
            leave="ease-in duration-200"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <div className="fixed inset-0 bg-black bg-opacity-25" />
          </Transition.Child>

          <div className="fixed inset-0 overflow-y-auto">
            <div className="flex min-h-full items-center justify-center p-4 text-center">
              <Transition.Child
                as={Fragment}
                enter="ease-out duration-300"
                enterFrom="opacity-0 scale-95"
                enterTo="opacity-100 scale-100"
                leave="ease-in duration-200"
                leaveFrom="opacity-100 scale-100"
                leaveTo="opacity-0 scale-95"
              >
                <Dialog.Panel className="w-full flex flex-row max-w-screen-md transform overflow-hidden rounded-2xl bg-white dark:bg-gray-900 p-6 text-left align-middle shadow-xl transition-all">
                  <div>
                    <button className="ml-auto" onClick={handleClose}>
                      <XCircleIcon className="h-10 w-10 text-gray-500" />
                    </button>
                  </div>
                  <Dialog.Title
                    as="h3"
                    className="text-lg font-medium leading-6 text-gray-900 dark:text-white "
                  >
                    {titleModal}
                  </Dialog.Title>

                  <div
                    className="inline-flex flex-col overflow-hidden w-full  mx-auto"
                    // style={{ maxHeight: "90vh" }}
                  >
                    {valueModal}
                  </div>
                </Dialog.Panel>
              </Transition.Child>
            </div>
          </div>
        </Dialog>
      </Transition>
    </>
  );
};
